.searchMovie {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s;

    &.on {
        opacity: 1;
        visibility: visible;

        .container {
            display: flex;
            flex-direction: column;

            h3 {
                margin-top: 50px;
                margin-bottom: 10px;
                font-size: 25px;
            }
            .content {
                display: flex;
                .left {
                    position: relative;
                    flex: 1;
                    li {
                        display: flex;

                        div {
                            &.img {
                                flex: 2;
                            }
                            &.txt {
                                flex: 3;
                                span {
                                    font-size: 25px;
                                    margin-right: 15px;
                                }
                                strong {
                                    vertical-align: 10%;
                                    font-weight: 300;

                                    &:hover {
                                        text-decoration: underline;
                                        cursor: pointer;
                                    }
                                }
                            }

                            p {
                                margin-bottom: 20px;
                            }

                            figure {
                                position: absolute;
                                top: 0;
                                display: none;
                                img {
                                    height: 215px;
                                    width: 155px;
                                }
                            }
                        }

                        &.on {
                            figure {
                                display: block;
                            }
                        }
                    }
                }

                .right {
                    flex: 1;
                    height: 280px;
                    transform: translateY(-15%);
                    input {
                        margin-bottom: 10px;
                        padding: 10px;
                        font-size: 19px;
                        color: #fff;
                        background: none;
                        border: none;
                        border-bottom: 1px solid #fff;
                        &::placeholder {
                            color: #fff;
                        }
                    }

                    li {
                        .autoComp {
                            display: flex;

                            .txt {
                                flex: 1;
                                span {
                                    &:hover {
                                        text-decoration: underline;
                                        cursor: pointer;
                                    }
                                }
                            }
                            .img {
                                flex: 1;
                            }
                            padding: 10px;
                            color: #fff;

                            img {
                                position: absolute;
                                top: 70px;
                                display: none;
                                width: 150px;
                            }
                        }
                        &.on {
                            img {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
}
