@import "../basic";

.Header {
    position: relative;
    display: flex;
    height: 91px;
    align-items: center;
    background: #1b1511;
    color: #fff;
    border-bottom: 1px solid var(--gl);

    nav {
        display: flex;
        justify-content: space-between;

        h1 {
            @include positionAbsCenter;

            img {
                width: 140px;
                height: 47px;
            }
        }

        .right {
            .top {
                justify-content: flex-end;
            }
            .icons {
                margin-left: 30px;
            }
        }

        .left {
            .icons {
                margin-right: 30px;
            }
        }

        .left,
        .right {
            display: flex;
            flex-direction: column;

            .top,
            .bottom {
                display: flex;

                .icons {
                    display: flex;
                    gap: 20px;
                }
            }

            .top {
                margin-bottom: 20px;
                display: flex;
                gap: 20px;
                font-size: 15px;
                font-weight: 300;
                color: #999999;

                li {
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            .bottom {
                display: flex;
                gap: 60px;
                font-size: 19px;
                .icons {
                    position: relative;
                    i {
                        &:hover {
                            cursor: pointer;
                        }
                    }
                    .searchI {
                        display: block;
                        &.on {
                            display: none;
                        }
                    }
                    .closeI {
                        display: block;
                        transform: scale(1.5);
                        &.on {
                            display: none;
                        }
                    }
                }
            }
        }
    }
    .searchMovie {
        position: absolute;
        bottom: 0;
        left: 0;
        transform: translateY(100%);
        width: 100%;
        height: 380px;
        background: #351f67d4;
        backdrop-filter: blur(2px);
        z-index: 999;

        ul {
            h3 {
                font-size: 21px;
            }
        }
    }
}
