.NewItem {
    .box {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 30px;

        .text {
            flex: 1;

            p {
                margin-bottom: 10px;
                font-size: 36px;
                color: #01738b;

                &:last-of-type {
                    color: #503396;
                }
            }

            span {
                font-size: 17px;
                line-height: 70px;
            }
        }
        .cardImg {
            flex: 1;
        }
    }
}
